.news-page {

  &__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap; }

  &__item {
    width: calc((100% / 3) - (80px / 3));

    @include mw(1200) {
      width: calc((100% / 3) - 10px); }

    @include mw(768) {
      width: 100%; } } }

.card {

  &__img-wrap--third {
    padding-top: 62%; } }
