.post {
  border-radius: 5px;
  overflow: hidden;

  & + .post {
    margin-top: 40px; }

  &:hover .post__block {
    background: #F7F7FA; }

  &__img-wrap {
    padding-top: 38%;
    position: relative;

    @include mw(600) {
      padding-top: 60%; } }

  &__img {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0; } }

.post-block {

  &__wrap {
    background-color: #fff;
    padding: 30px;
    display: flex;
    border: 1px solid #E0E3EE;
    border-top: none;
    border-radius: 0 0 5px 5px;
    transition: 0.2s;

    @include mw(600) {
      flex-direction: column;
      align-items: center;
      padding-top: 20px;
      padding-left: 15px;
      padding-right: 15px;
      padding-bottom: 20px; } }

  &__brand {
    width: 98px;
    height: 80px;
    border: 1px solid #E0E3EE;
    border-radius: 5px;
    margin-right: 30px;
    background-position: center center;
    background-repeat: no-repeat;
    flex-shrink: 0;

    @include mw(600) {
      margin-bottom: 20px; } }

  &__box {
    display: flex;
    flex-direction: column;
    justify-content: space-between; }

  &__title {
    font-weight: 600;
    color: $text;
    text-align: center;
    margin-bottom: 20px; }

  &__service {
    display: flex;
    align-items: center; }

  &__value {
    background: #F3F4FB;
    border-radius: 5px;
    padding: 6px 10px;
    margin-right: 10px;
    font-weight: 500;
    font-size: 0.875rem;
    color: #ACAFC2; }

  &__date {
    font-size: 0.875rem;
    color: #000000; } }

.albums {

  @include mw(768) {
    margin-bottom: 30px; }

  &__inner {
    background: #F7F7FA;
    border: 1px solid #E0E3EE;
    border-radius: 10px;

    @include mw(600) {
      border: none; } }

  &__title {
    font-weight: 600;
    font-size: 1rem;
    color: $text;
    text-transform: capitalize;

    @include mw(768) {
      display: none; }

    &--top {
      border: none;
      border-bottom: 1px solid #E6E6EA;
      font-size: 1.125rem;
      padding: 30px;

      @include mw(768) {
        display: block; }

      @include mw(600) {
        border: 1px solid #E6E6EA;
 } } }        // border-bottom: none

  &__list {
    @include mw(768) {
      display: flex;
      flex-wrap: wrap; }

    @include mw(600) {
      border-radius: 0 0 5px 5px; } }

  &__item {
    @include mw(768) {
      width: calc(100% / 8);

      & + li {
        border-left: 1px solid #E6E6EA;

        @include mw(600) {
          border-left: none;
 } } }          // border: 1px solid #E6E6EA

    @include mw(600) {
      width: calc( 100% / 3 );
      border: 1px solid #E6E6EA;
      border-left: none;

      &:nth-child(3n+3) {
        border-right: none; }

      &:nth-child(n+4) {
        border-top: none; } }

    & + .albums__item {
      border-top: 1px solid #E6E6EA;

      @include mw(600) {
 } } }        // border-top: none

  &__link {
    display: flex;
    align-items: center;
    padding-left: 20px;
    transition: 0.2s;
    padding-top: 19px;
    padding-bottom: 20px;

    @include mw(768) {
      padding: 0;
      min-height: 78px;
      height: 100%; }

    &:hover {
      background-color: #fff; } }

  &__img-wrap {
    width: 75px;
    height: 40px;
    background-repeat: no-repeat;
    background-position: center center;
    margin-right: 25px;

    @include mw(768) {
      // width: 100%
      height: 50px;
      margin-right: 0;
      flex-grow: 1; } } }

.image-main {
  background-image: url(/img/svg/a1.svg);
  background-size: 36px 30px; }
