.documents-accordion {
  background: #fff;
  border: 1px solid #e0e3ee;
  border-radius: 5px;
  cursor: pointer;

  & + div {
    margin-top: 20px; }

  &__control {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &[data-control] {
      padding-top: 28px;
      padding-bottom: 28px; } }

  &__title {
    font-weight: 600;
    margin-right: auto;
    width: 180px; }

  &__icon {
    width: 19px;
    height: 24px;
    background-image: url(/img/svg/copy.svg);
    background-size: contain;

    @include mw(600) {
      display: none; } }

  &__info {
    font-weight: 500;
    font-size: 0.875rem;
    color: #B0B3C6;
    margin-left: 10px;
    margin-right: 56px;

    @include mw(600) {
      display: none; } }

  &__arr {
    width: 20px;
    height: 20px;
    background-image: url(/img/svg/arr.svg);
    transition: 0.4s; }

  &__link {
    display: flex;
    align-items: center;
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 20px;
    padding-bottom: 20px;
    border-bottom: none;

    @include mw(600) {
      padding-left: 15px;
      padding-right: 15px; }

    & + a {
      border-top: 1px solid #E0E3EE; } }

  &__link-icon {
    background-image: url(/img/svg/copy-1.svg);
    width: 15px;
    height: 20px;
    background-size: contain;

    @include mw(600) {
      display: none; } }

  &__link-doc {
    font-size: 1rem;
    font-weight: 600;
    color: $text;
    margin-left: 20px;

    @include mw(600) {
      margin-left: 0; } }

  &__link-download {
    font-size: 0.875rem;
    color: #B0B3C6;
    margin-left: auto; } }

