.promo {

  &__title {
    width: 100%;
    text-align: left; }

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 10px; }

  &__link-news {
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1rem;
    font-weight: 500;
    color: #B0B3C6;
    border: 1px solid #E3E5EF;
    border-radius: 4px;
    margin-bottom: 64px;
    transition: 0.2s;

    &:hover {
      opacity: 0.5; }

    & span {
      margin-right: 10px; }

    & svg {
      stroke: #B0B3C6;
      fill: transparent;
      transform: translateY(2px); } } }

.column-wrap {
  display: flex;
  justify-content: space-between;

  @include mw(768) {
    flex-direction: column; }

  &--reverse {
    @include mw(768) {
      flex-direction: column-reverse; } } }

.title {
  margin-bottom: 30px;
  font-weight: 600;
  font-style: normal;
  text-align: left; }

.column-left {
  flex-grow: 1;
  max-width: 680px;
  min-width: 0;

  @include mw(768) {
    max-width: none;
    margin-bottom: 30px; } }

.card {
  background: #FFFFFF;
  border: 1px solid #E0E3EE;
  border-radius: 5px;
  display: block;
  overflow: hidden;
  margin-bottom: 20px;

  &__img-wrap {
    position: relative;
    width: 100%;

    &--wide {
      padding-top: 38%;

      @include mw(600) {
        padding-top: 60%; } }

    &--half {
      padding-top: 60%;

      @include mw(600) {
 } } }        // padding-top: 38%

  &__img {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0; }

  &__content {
    padding: 20px 30px;

    @include mw(600) {
      padding-left: 15px;
      padding-right: 15px; } }

  &__title {
    font-weight: 600;
    color: $text;
    margin-bottom: 20px; }

  &__info {
    display: flex;
    align-items: center; }

  &__category {
    font-weight: 500;
    font-size: 0.875rem;
    color: #ACAFC2;
    padding: 6px 10px;
    background: #F3F4FB;
    border-radius: 5px;
    margin-right: 20px;

    @include mw(600) {
      font-size: 1rem; } }

  &__date {
    ont-weight: 300;
    font-size: 0.875rem;
    color: $black; }

  &--wide {
    width: 100%; }

  &--half {
    width: calc(50% - 10px);

    @include mw(600) {
      width: 100%; } }

  &:hover {
    background: #F7F7FA;
    transition: 0.2s; } }

.promo-slider {
  background: #FCFCFF;
  border: 1px solid #E0E3EE;
  border-radius: 5px;
  padding-top: 30px;
  padding-left: 30px;
  padding-bottom: 33px;
  width: 100%;

  @include mw(600) {
    padding-left: 15px;
    padding-right: 15px; }

  &__title {
    margin-bottom: 25px;
    font-weight: 600; }

  &__wrap {
    width: 100%; }

  & .slick-arrow {

    @include mw(600) {
      display: none !important; } } }

.private-card {
  background: #FFFFFF;
  border: 1px solid #E0E3EE;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 30px;
  padding-bottom: 30px;
  padding-left: 15px;
  padding-right: 15px;
  transition: 0.2s;

  &:hover {
    background: #f7f7fa; }

  &__img-wrap {
    margin-bottom: 10px; }

  &__title {
    font-weight: 600;
    color: $text;
    margin-bottom: 10px;

    @include mw(600) {
      text-align: center; } }

  &__proff {
    font-size: 0.875rem;
    color: #B0B3C6;
    font-weight: 500;

    @include mw(600) {
      text-align: center; } } }

.private-card--slide {
  border-radius: 5px;
  width: 228px;
  margin-left: 20px; }

.column-right {
  width: 340px;
  flex-shrink: 0;
  margin-left: 20px;

  @include mw(1200) {
    width: 280px; }

  @include mw(768) {
    margin-left: 0;
    width: auto; } }

.promo-birtday {
  background: #FFFFFF;
  border: 1px solid #E0E3EE;
  border-radius: 5px;
  margin-bottom: 34px;

  &__head {
    padding-top: 30px;
    padding-left: 30px;
    // padding-right: 30px
    padding-bottom: 17px;
    display: flex;
    align-items: center;

    @include mw(600) {
      padding-left: 15px;
      padding-right: 15px; } }

  &__date {
    font-size: 0.875rem;
    font-weight: 600;
    color: $white;
    padding: 12px 9px;
    background: #222124;
    border-radius: 5px;
    margin-right: 1rem; }

  &__title {
    font-weight: 600;
    font-size: 1rem; }

  &__img-wrap {
    position: relative;
    padding-top: 40%; }

  &__img {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0; } }

.coworker {

  &__item {
    display: flex;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 30px;
    padding-right: 30px;
    transition: 0.2s;
    border-top: 1px solid #E0E3EE;

    @include mw(768) {
      padding-top: 15px;
      padding-bottom: 15px;
      padding-left: 15px;
      padding-right: 15px; }

    &:hover {
      background: #F7F7FA; } }

  &__item--promo {}

  &__img-wrap {
    width: 48px;
    height: 48px;
    margin-right: 20px;
    flex-shrink: 0; }

  &__name {
    font-weight: 600;
    font-size: 0.875rem;
    margin-bottom: 5px;
    color: $text;

    @include mw(600) {
      // font-size: 18px
      margin-bottom: 10px; } }

  &__proff {
    font-weight: 500;
    font-size: 0.75rem;
    color: #B0B3C6;

    @include mw(600) {
 } } }      // font-size: 1rem

.promo-library {
  background: #FCFCFF;
  border: 1px solid #E0E3EE;
  box-sizing: border-box;
  border-radius: 5px;
  padding: 30px 30px 30px 30px;

  @include mw(600) {
    padding-left: 15px;
    padding-right: 15px; }

  &__title {
    font-weight: 600;
    margin-bottom: 30px; }

  &__book {

    margin-bottom: 38px; }

  &__link {
    font-weight: 500;
    font-size: 1rem;
    color: #B0B3C6;
    transition: 0.2s;

    &:hover {
      opacity: 0.5; }

    & .icon {
      margin-left: 10px; }

    & svg {
      stroke: #B0B3C6;
      transform: translateY(4px);
      fill: transparent; } } }

.book {
  display: flex;
  flex-direction: column;
  background: #FFFFFF;
  border: 1px solid #E0E3EE;
  box-shadow: 0px 5px 20px #EFEFFC;
  border-radius: 5px;
  transition: 0.2s;

  &:hover {
    box-shadow: none; }

  &__inner {
    display: flex;
    justify-content: center;
    padding-top: 30px;
    padding-bottom: 30px;
    border-bottom: 1px solid #F6F6F9; }

  &__cont {
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 30px;
    padding-top: 13px;

    @include mw(768) {
      padding-left: 15px;
      padding-right: 15px;
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center; } }

  &__img-wrap {
    align-self: center; }

  &__section {
    font-weight: 500;
    font-size: 0.75rem;
    color: #ACAFC2;
    background: #F3F4FB;
    border-radius: 2px;
    padding: 5px 8px;
    margin-bottom: 13px;
    display: inline-block;

    @include mw(600) {
      font-size: 1rem; } }

  &__title {
    margin-bottom: 5px;
    font-weight: 600;
    font-size: 1rem;
    color: #000000; }

  &__autor {
    font-weight: 500;
    font-size: 0.75rem;
    color: #90969E; } }

.slick-initialized .slick-slide {
  display: flex; }

.slick-arrow {
  cursor: pointer;
  width: 20px;
  height: 20px;
  background-image: url(/img/svg/arr.svg);
  font-size: 0;
  background-color: transparent;
  position: absolute;
  top: -47px;
  right: 30px;
  transition: 0.2s;

  &:hover {
    opacity: 0.5; } }

.slick-prev {
  transform: rotate(180deg);
  right: 60px; }
