.main-tabs {

  &__list {
    height: 80px;
    display: flex;
    justify-content: space-between;
    border: 1px solid #E0E3EE;
    border-radius: 0 0 5px 5px;

    @include mw(600) {
      // flex-wrap: wrap
      // max-height: 0
 }      // overflow: hidden

    & .main-tabs__link {} }

  &__item {
    width: calc(100% / 7);

    @include mw(600) {
      width: calc( 100% / 3 );
      border: 1px solid #E0E3EE;
      border-left: none;

      &:nth-child(3n+3) {
        border-right: none; }

      &:nth-child(n+4) {
        border-top: none; } }

    & + li {
      border-left: 1px solid #E0E3EE;

      @include mw(600) {
        border-left: none; } } }

  &__link {
    background-repeat: no-repeat;
    display: block;
    height: 100%;
    width: 100%;
    background-position: center center;
    transition: 0.2s;

    @include mw(600) {
      min-height: 78px;
      height: 100%; }

    &:hover {
      background-color: #fff; } } }

.sub-tabs {

  &__accordion {
    background: #FFFFFF;
    border: 1px solid #E0E3EE;
    border-radius: 5px;

    & .sub-tabs__img-wrap {

      @include mw(600) {
        background-size: contain;
        // max-width: 45px
        margin-right: 15px; } }

    & +div {
      margin-top: 20px; } }

  &__title {
    font-weight: 600;
    margin-right: auto;
    max-width: 180px;

    @include mw(600) {
      max-width: 180px; } }

  &__head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer; }

  &__content {
    display: flex;
    flex-wrap: wrap; }

  &__img-wrap {
    width: 86px;
    height: 48px;
    background-repeat: no-repeat;
    background-position: 0 center;
    order: -1;

    &.image-jaguar {
      transform: translateX(-10px); } }

  &__arr-wrap {
    width: 20px;
    height: 20px;
    background-image: url(/img/svg/arr.svg);
    transition: 0.4s; }

  &__man-wrap {
    width: 80px;
    height: 40px;

    @include mw(1250) {
      margin-right: 20px; }

    @include mw(768) {
      display: none; } }

  &__man-img {

    & + img {
      margin-left: -20px; } }

  &__info {
    margin-left: 20px;
    margin-right: 50px;
    font-weight: 500;
    font-size: 0.875rem;
    color: #B0B3C6;

    @include mw(1250) {
      display: none; } } }

.open {

  & .sub-tabs__arr-wrap {
    transform: rotate(90deg); } }

.private-card {

  &--structure {
    width: calc(100% / 3);
    border-width: 0.5px;

    @include mw(600) {
      width: 100%;
      margin-bottom: 20px; }

    &:hover {
      background-color: #F7F7FA; } } }

.image-jaguar {
  background-size: 86px 48px;
  background-image: url(/img/jaguar.png); }

.image-land-rover {
  background-size: 65px 32px;
