.image-ford {
  background-size: 75px 28px;
  background-image: url(/img/ford.png); }

.image-mazda {
  background-size: 47px 39px;
  background-image: url(/img/mazda.png); }

.image-volvo {
  background-size: 40px 40px;
  background-image: url(/img/volvo.png); }

.image-merseded {
  background-size: 43px 40px;
  background-image: url(/img/merseded.png); }

.image-porsche {
  background-size: 31px 40px;
  background-image: url(/img/porsche.png); }

.r-tabs {

  & .main-tabs__list {
    margin-bottom: 20px;

    @include mw(768) {
      display: flex;
      // height: 50px
 } }      // flex-wrap: wrap

  & .main-tabs__content {
    padding: 0; }

  & .r-tabs-state-active {

    & .main-tabs__link {
      background-color: #fff; } } }

[data-accordion]>[data-control]:after {
  background-image: none; }

[data-accordion] [data-control] {
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 15px;
  padding-bottom: 16px;
  border-bottom: none;

  @include mw(600) {
    padding-left: 15px;
    padding-right: 15px; } }

.brand-tab {
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #E0E3EE;
  border-radius: 5px 5px 0 0;
  border-bottom: none;

  &__img {
    width: 165px;
    height: 30px; }

  @include mw(600) {
    position: relative;
    border-radius: 5px;
    border-bottom: 1px solid #e0e3ee;

    &::before {
      content: "";
      position: absolute;
      width: 20px;
      height: 20px;
      background-image: url(/img/svg/arr.svg);
      top: 50%;
      transform: translateY(-50%);
      right: 15px; }

    &--active {
      border-radius: 5px 5px 0 0;
      border-bottom: none;

      &::before {
        transform: translateY(-50%) rotate(90deg); } } } }

.brand-block {

  @include mw(600) {
    border: none;

    flex-wrap: wrap;
    max-height: 0;
    overflow: hidden;

    &--active {
      height: auto;
      max-height: 240px;
      border: 1px solid #e0e3ee;
      border-top: none; } } }
