.anonymous {
  margin-bottom: 100px;

  @include mw(600) {
    margin-bottom: 50px; }

  &__inner {
    background: url(/img/hat.png), linear-gradient(180deg, #222124 0%, #262628 100%);
    background-repeat: no-repeat;
    background-size: auto 100%, 100% 100%;
    background-position: -60px 0, 0 0;
    border-radius: 10px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-top: 87px;
    padding-bottom: 87px;
    position: relative;
    overflow: hidden;

    @include mw(768) {
      flex-direction: column;
      padding-left: 15px;
      padding-right: 15px;
      padding-top: 50px;
      padding-bottom: 50px;
      align-items: stretch;
      background-position: center 0, 0 0;
      border-radius: 10px;

      &::after {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        background-color: #000;
        opacity: 0.3; } } }

  &__picture {
    width: 45%;
    position: absolute;
    left: 0;
    bottom: 0;

    @include mw(768) {
      display: none;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      bottom: auto;
      width: 60%; } }

  &__img-wrap {
    width: 100%;
    padding-top: 52%;
    position: relative;
    left: 0; }

  &__img {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0; }

  &__text {
    font-weight: 600;
    color: #FFFFFF;
    width: 233px;
    text-align: center;
    margin-right: 6.5%;
    z-index: 1;

    @include mw(1200) {
      margin-right: 30px; }

    @include mw(768) {
      width: auto;
      margin-right: 0;
      margin-bottom: 50px; } }

  &__cont {
    margin-right: 6.5%;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include mw(1200) {
      margin-right: 30px; }

    @include mw(768) {
      margin-right: 0; } }

  &__cont-text {
    font-weight: 300;
    font-size: 1rem;
    line-height: 18px;
    text-align: center;
    color: #B0B3C6;
    width: 179px;
    margin-bottom: 20px;

    @include mw(768) {
 }      // color: #fff

    @include mw(600) {
      margin-bottom: 20px; } }

  &__link {
    font-weight: 500;
    color: #B0B3C6;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 20px;
    padding-right: 15px;
    border: 1px solid #3D3E48;
    border-radius: 4px;
    display: flex;
    align-items: center;

    @include mw(768) {
 }      // color: #fff

    @include mw(600) {
      width: 100%;
      justify-content: center; }

    &:hover {
      opacity: 0.5; }

    & .icon {
      display: block;
      margin-left: 10px;

      & svg {
        fill: transparent;
        stroke: #B0B3C6;
        transform: translateY(3px); } } } }
